<template>
    <div>
        <top-panel-menu></top-panel-menu>
        <v-toolbar color="primary" extended extension-height="4" app>
            <v-toolbar-side-icon
                    @click.stop="$store.dispatch('switchMenu')"></v-toolbar-side-icon>
            <v-toolbar-title class="headline text-uppercase">
                <!--<span>SMM</span>-->
                <span class="font-weight-light">Real</span>
                <span class="font-weight-thin">{{this.$route.meta.title}}</span>
            </v-toolbar-title>
            <v-spacer></v-spacer>
            <top-panel-settings></top-panel-settings>
            <top-panel-user></top-panel-user>
            <template v-slot:extension>
                <v-progress-linear slot="extension" v-model="loadingPercent" v-show="loading" :indeterminate="false" class="px-0"
                                   color="secondary" height="4"></v-progress-linear>
            </template>

        </v-toolbar>
    </div>
</template>

<script>
    import TopPanelMenu from "@/components/topPanel/topPanelMenu";
    import TopPanelUser from "@/components/topPanel/topPanelUser";
    import TopPanelSettings from "@/components/topPanel/topPanelSettings";
    export default {
        name: "topPanel",
        components: {TopPanelSettings, TopPanelUser, TopPanelMenu},
        data(){
          return{
              drawer: false,
              loadingPercent: 0,
              loadTime: 0,
              interval: null,
              loading: true,
          }
        },
        created() {
            let perfData = window.performance.timing;
            let estimatedTime = Math.abs(perfData.loadEventEnd - perfData.navigationStart);
            this.loadTime = parseInt((estimatedTime / 1000) % 60) * 100;
            this.doProgress();
        },
        watch: {
            loadingPercent(val) {
                if (val >= 100) {
                    this.loading = false;
                    clearInterval(this.interval);
                }
            },
        },
        methods: {
            doProgress() {
                let step = this.loadTime / 100;
                this.interval = setInterval(() => {
                    this.loadingPercent++
                }, step);
            }
        }
    }
</script>

